import server from '../../server.js';
export default {
	data() {
		return {
			MsgList:[],
			auid:0,
			val:'',
			value:"",
			tuijianren:0
		}
	},
	onLoad:function(e){
		console.log(e);
		console.log('推荐人：'+e.tuijianid);
		this.getMyMsg()
		if(e.tuijianid){
			this.tuijianren=e.tuijianid;
			server.tgid=this.tuijianren;
		}
		uni.login({
		    provider: 'weixin',
		    success: (res) => {
				console.log(res);
				uni.request({
					method:'POST',
					url: 'https://api.angeli.top/reg.php?type=wxlogin', //仅为示例，并非真实接口地址。
					data: {
						code: res.code,
						tuijianId:this.tuijianren,
						
					},
					header: {
						'content-type': 'application/x-www-form-urlencoded'
					},
					success: (res) => {
						console.log(res);
						server.usersk=res.data.data.session_key
						server.cookie=res.header['Set-Cookie'];
						if(res.data.code!="0"){
							console.log('已经存在有账号了，就不要注册了');
							this.tuijianren=0
							console.log(this.tuijianren)
							server.userinfo=res.data.data;
						}
						console.log('输出登录结果')
						console.log(res)
					},
				});
		    }
		})
		
		
	},
	onShow:function(){
		
	},
	methods: {
		
		
		getreg:function(){
			uni.navigateTo({
				url: '../reg/reg'
			})
		},
		input:function(e){
			this.val=e.target.value 
		},
		login:function(){
			
		},
		getme:function(){
			uni.navigateTo({
				url: '../i/i'
			})
		},
		markmsg:function(){
			uni.request({
				method:'GET',
				url: "https://api.angeli.top/user.php?type=mark&class=system", //请求标记已读消息
				data: {
					'msgId':this.msgid,
					token:server.token
				},
				header: {
					'content-type': 'application/x-www-form-urlencoded',
					
				},
				success: (res) => {
					console.log(res)
					if(res.data.code=="1"){
						console.log('已将信息标记为已读')
					}else{
						console.log('标记已读失败！')
					}
					
				},
				complete() {
					
				}
			});
		},
		InputFocus(e) {
			this.InputBottom = e.detail.height
		},
		InputBlur(e) {
			this.InputBottom = 0
		},
		getMyMsg:function(){
			uni.request({
				method:'GET',
				url: "https://api.angeli.top/user.php?type=getMyTG", 
				data: {
					token:server.token
				},
				header: {
					'content-type': 'application/x-www-form-urlencoded',
					
				},
				success: (res) => {
					console.log(res)
					if(res.data.code=="1"){
						this.MsgList=res.data.data
					}
					console.log(this.MsgList)
				}
			});
		}
	},
	onShareAppMessage(res) {
		console.log('MyId:'+server.userinfo.Auid)
	   if (res.from === 'button') {// 来自页面内分享按钮
	      console.log(res.target)
	    }
	    return {
	      title: '给你安利一个好东西',
	      path: '/pages/menu/huodong?tuijianid='+server.userinfo.Auid,
		  
	    }
	}
}
